import Vue from "vue";
import VueRouter from "vue-router";
import Menu from "./menu/Menu";
import Game from "./game/Game";

Vue.use(VueRouter);

let router = new VueRouter({
  routes: [
    {
      name: "game",
      path: "/game/:mode",
      component: Game
    },
    {
      name: "menu",
      path: "/*",
      component: Menu
    }
  ]
});

new Vue({
  el: "#app",
  router
});



