<template>
  <div class="game">
      <div class="game-header">
        <GamePlayer :axis="'x'"></GamePlayer>
        <div v-if=!gameInProgress
          class="cap-center"
          :class="{ clickable: !gameInProgress }"
          @click=restartGame >
          {{ startBtnCaption }}
        </div>
        <GamePlayer :axis="'y'"></GamePlayer>
      </div>
      <GameBoard></GameBoard>
    </div>

</template>

<script>
import GameBoard from "./GameBoard";
import GamePlayer from "./GamePlayer";
import newGameStore from "./GameStore";
import { mapState, mapGetters, mapActions, mapMutations } from "Vuex";

let defaultMapSize = 8; // NxN cells with values from -N to N, except zeros. bugged if N > 9

export default {
  components: {
    GameBoard,
    GamePlayer
  },
  data() {
    return {
      mapSize: defaultMapSize
    };
  },
  store: newGameStore(defaultMapSize),
  computed: {
    startBtnCaption() {
      return this.gameFinished ? "Restart!" : "Start!";
    },
    ...mapState([
      "gameMode",
      "gameStarted",
      "boardMap",
      "cellsById",
      "turnHistory",
      "firstPlayer",
      "firstCell",
      "players",
      "playerLeft"
    ]),
    ...mapGetters([
      "activeCells",
      "activeRow",
      "gameFinished",
      "gameInProgress",
      "secondPlayer",
      "lastCellId",
      "currentPlayer",
      "winner"
    ])
  },
  methods: {
    initGameMode() {
      let mode = this.$route.params.mode;
      if (mode === "hotseat") {
        this.setMode({
          mode,
          players: {
            x: {
              axis: "x",
              name: " ",
              score: 0
            },
            y: {
              axis: "y",
              name: "Anon Y",
              score: 0
            }
          }
        });
      }
    },
    ...mapMutations(["setMode"]),
    ...mapActions(["createNewMap", "restartGame", "makeTurn"])
  },
  mounted() {
    this.initGameMode();
    if (!this.boardMap.length) {
      this.createNewMap();
    }
  }
};
</script>

<style lang="scss">
@import "../vars";

.game {
  display: flex;
  flex-direction: column;
  border: solid #fff4 1px;
  border-radius: 1vmin;
  font-family: $main-font;
  margin: 0.5vmin auto;
  max-width: 98vmin;
  height: 98vmin;
  text-align: center;
  padding: 0.3vmin;
}

.game-header {
  color: #eee;
  display: flex;
  padding: 0.3vmin;
  font-size: 5vmin;
  height: 6.4vmin;
  .cap-center {
    padding: 0 1.5vmin;
    &.clickable {
      background: forestgreen;
    }
  }
}
</style>



